import {Component} from '@angular/core';
import {Post} from '../../models/Post';
import {PostService} from "../../services/post.service";
@Component({
	moduleId: module.id,
	selector: 'my-post',
	templateUrl: 'post.component.html',
	providers: [PostService]
})

export class PostComponent {
	totalPosts: Post[];

	posts: Post[];

	currentPage = 1;
	pageLimit = 3;
	start = 1;
	end = 1;
	pageCount = 1;



	constructor(private postService: PostService) {
		this.postService.getAll().subscribe(posts => {
			this.totalPosts = posts;
			this._initStartEnd();
		});

	}

	setCurrentPage = (page) => {
		this.currentPage = page;
		this._initStartEnd();
	};

	previousPage = () => {
		if (this.currentPage != 1) {
			this.currentPage -= 1;
			this._initStartEnd();
		}
	};
	nextPage = () => {
		if (this.currentPage < this.pageCount) {
			this.currentPage += 1;
			this._initStartEnd();
		}
	};

	_initStartEnd() {
		let count = this.totalPosts.length;
		this.end = this.currentPage * this.pageLimit;
		this.start = this.end - this.pageLimit + 1;
		if (this.end > count) {
			this.end = count;
		}
		this.posts = this._range(this.totalPosts, this.start, this.end);
	}

	_range(array, start, end) {
		start -= 1;
		let results = [];
		for (let x = start; x < end; x++) {
			let item = array[x];
			if (item) {
				results.push(item);
			}
		}
		return results;
	}

	getPageSize() {
		let input = [];
		this.pageCount = Math.ceil(this.totalPosts.length / this.pageLimit);
		for (let i = 1; i <= this.pageCount; i++) {
			input.push(i);
		}
		return input;
	};


}



