import {NgModule, ModuleWithProviders} from '@angular/core';
import {CommonModule} from '@angular/common';
import {Routes, RouterModule} from '@angular/router';
import {PostComponent} from "./post.component";
import {PostItemComponent} from "./post-item.component";
import {SlideModule} from "../../components/slide/slide.module";

//Định nghĩa router riêng cho module này
const routing: Routes = [
  {
    path: '',
    component: PostComponent
  },
  {
    path: ':slug',
    component: PostItemComponent
  }
];

//forChild -> Vì router này sẽ được load như một router con
//cho nên chúng ta định nghĩa forChild cho router này
const Routing: ModuleWithProviders = RouterModule.forChild(routing);


@NgModule({
  declarations: [PostComponent,PostItemComponent],
  imports: [CommonModule, Routing, SlideModule
  ],
  providers: [],
})
export class PostsModule {
}



